import { StatusBar } from "expo-status-bar";
import React, { useState, useEffect } from "react";
import {
  Text,
  View,
  TouchableOpacity,
  ScrollView,
  Dimensions,
  TextInput,
  Image,
} from "react-native";
import { useFocusEffect } from "@react-navigation/native";
import BottomNav from "../components/BottomNav";
import TopBar from "../components/TopBar";
import { getImages } from "../services/storage";
import { getUserInfo, getUserUid } from "../services/firestore_user";
import styles from "../styles/StoreStyles";
import { FontAwesome } from "@expo/vector-icons";

const { width: SCREEN_WIDTH } = Dimensions.get("window");

export default function Store({ navigation }) {
  // true  , false   
  const [isSelling, setIsSelling] = useState(true);

  const [name, setName] = useState(null); // 
  const [image, setImage] = useState(null); //  
  const [style, setStyle] = useState([]); //  
  const [bio, setBio] = useState(null); //  

  //    
  const [sellingItem, setSellingItem] = useState([]);
  //    
  const [soldItem, setSoldItem] = useState([]);

  useFocusEffect(
    React.useCallback(() => {
      const fetchUserName = async () => {
        try {
          const userInfo = await getUserInfo();
          setName(userInfo.name);
          setBio(userInfo.bio);
          setImage(userInfo.image);
          if (userInfo.style) setStyle(userInfo.style);
        } catch (error) {
          console.log(error);
        }
      };

      fetchUserName();
    }, [])
  );

  useFocusEffect(
    React.useCallback(() => {
      const fetchImages = async () => {
        try {
          const uid = await getUserUid();
          const imagesList = await getImages(uid);
          setSellingItem(imagesList);
        } catch (error) {
          console.log(error);
        }
      };

      fetchImages();
    }, [])
  );

  //   
  const renderSelectedStyles = () => {
    return (
      <ScrollView horizontal style={styles.selectedItemsContainer}>
        {style.map((item, index) => (
          <View key={index} style={styles.selectedItem}>
            <Text style={styles.selectedItemText}>{item}</Text>
          </View>
        ))}
      </ScrollView>
    );
  };

  return (
    <View style={styles.container}>
      <StatusBar style="auto" />

      <TopBar navigation={navigation} />

      <ScrollView style={styles.body}>
        <View style={styles.infoBox}>
          <View style={styles.userInfo}>
            {image == null ? (
              <View style={styles.userImage}></View>
            ) : (
              <Image style={styles.userImage} source={{ uri: image }} />
            )}

            <TouchableOpacity
              onPress={() => {
                navigation.navigate("Info");
              }}
              style={styles.gearIcon}
            >
              <FontAwesome name="gear" size={16} color="darkgray" />
            </TouchableOpacity>
            <Text style={styles.storeText}>{name}</Text>
          </View>

          <View style={styles.storeInfo}>
            <View>
              <Text style={styles.storeText}></Text>
              <View>{renderSelectedStyles()}</View>
            </View>

            <View>
              <Text style={styles.storeText}></Text>
              <TextInput
                editable={false}
                placeholder="   :("
                multiline={true}
                numberOfLines={3}
                ellipsizeMode="tail"
                value={bio}
                style={styles.bioBox}
              />
            </View>
          </View>
        </View>

        <View style={styles.followBox}>
          <TouchableOpacity style={styles.follow}>
            <Text style={styles.followText}>Followers</Text>
            <Text>10</Text>
          </TouchableOpacity>

          <TouchableOpacity style={styles.follow}>
            <Text style={styles.followText}>Following</Text>
            <Text>15</Text>
          </TouchableOpacity>
        </View>

        <View>
          <View style={styles.buttonBox}>
            <TouchableOpacity
              onPress={() => setIsSelling(true)}
              style={
                isSelling == true
                  ? styles.clickedButton
                  : styles.nonClickedButton
              }
            >
              <Text style={styles.btnText}>Selling</Text>
            </TouchableOpacity>
            <TouchableOpacity
              onPress={() => setIsSelling(false)}
              style={
                isSelling == false
                  ? styles.clickedButton
                  : styles.nonClickedButton
              }
            >
              <Text style={styles.btnText}>Sold</Text>
            </TouchableOpacity>
          </View>

          <View style={styles.itemBox}>
            {isSelling == true
              ? sellingItem.map((value, index) => (
                  <TouchableOpacity
                    onPress={() =>
                      navigation.navigate("Detail", { src: value })
                    }
                    key={index}
                    style={styles.item}
                  >
                    <Image style={styles.item} source={{ uri: value }} />
                  </TouchableOpacity>
                ))
              : soldItem.map((value, index) => (
                  <TouchableOpacity
                    onPress={() =>
                      navigation.navigate("Detail", { src: value })
                    }
                    key={index}
                    style={styles.item}
                  >
                    <Text>{value}</Text>
                  </TouchableOpacity>
                ))}
          </View>
        </View>
      </ScrollView>

      <BottomNav navigation={navigation} />
    </View>
  );
}
